import React from 'react';
import FormElement from '../FormElement';
import PhoneNumber from '../PhoneNumber';
import AppComponent from '../AppComponent';
import {Row, Button} from 'react-bootstrap';
import {getIndustrySegments, getBusinessSegments, getDepartments} from '../../services/MasterData';
import {registerManufacturer} from '../../services/registration';
import {FormGroup,ControlLabel,FormControl, Alert} from 'react-bootstrap';
import {isValidForm, highlightError} from '../utilities';
import {validateEmail, validateGST, validatePincode, validateWebsite, validateMobileNumber} from '../../utils/validator';
import $ from 'jquery';
import {getGeneralDetails, saveGeneralDetails} from '../../services/profile';

class GeneralDetails extends AppComponent {

  constructor(props) {
    super(props);
    this.initState();
    this.handleSubmit = this.handleSubmit.bind(this);
    this.setError = this.setError.bind(this);
    this.handleDepartment = this.handleDepartment.bind(this);
  }

  async componentDidMount() {
    if(this.props.isRegister == false) {
      let generalDetails = await getGeneralDetails(this.state.auth.orgId);
      this.setState({'inputs':generalDetails});
    }
  }

  async initState() {
    const industrySegments = await getIndustrySegments();
    const businessSegments = await getBusinessSegments();
    const departments = await getDepartments();
    this.setState({
      'industrySegments': industrySegments,
      'businessSegments': businessSegments,
      'departments': departments
    });
  }

  handleDepartment(event) {
    const val = event.target.value;
    $('#department_text').val(val);
  }

  setError(errorMessage) {
    this.setState({hasError:true,errorMsg:errorMessage});
  }

  validate() {
    if (isValidForm('registrationForm')) {
      var isValid = true;
      if (validateEmail(this.state.inputs.emailId) === false) {
        highlightError('emailId');
        isValid = false;
      }
      if (this.props.isRegister && validateGST(this.state.inputs.gstNo) === false) {
        highlightError('gstNo');
        isValid = false;
      }
      if (validatePincode(this.state.inputs.pincode) === false) {
        highlightError('pincode');
        isValid = false;
      }
      if (validateWebsite(this.state.inputs.website) === false) {
        highlightError('website');
        isValid = false;
      }
      if (validateMobileNumber(this.state.inputs.mobileNumber) === false) {
        highlightError('mobileNumber');
        isValid = false;
      }
      return isValid;
    }
    return true;
  }

  async handleSubmit() {
    this.setState({hasError:false});
    if(isValidForm('registrationForm') && this.validate()) {
      if(this.props.isRegister) {
        registerManufacturer(this.state.inputs).then(res => this.callback(res));
      } else {
        saveGeneralDetails(this.state.inputs).then(res => this.callback(res));
      }
    } else {
      this.setError('Please correct the highlighted fields.');
    }
  }

  callback(res) {

      if(res.status === 409) {
        res.json().then(responseBody => {
          let errCode = responseBody.errorCode;
          if(errCode === "ERR_USR_EXST") {
            this.setError('User already exists.')
          } else if (errCode === "ERR_ORG_EXST") {
            this.setError('Organisation already exists.')
          }
        });
      } else if (res.status !== 200) {
        // show general error
        this.setError('Technical issue. Please contact system admin.')
      } else if (res.status === 200) {
        if(this.props.isRegister) {
          this.setError('User Registered');
        } else {
          this.setError('Details saved successfully');
        }
      }

  }

  render() {

    const industrySegments = this.state.industrySegments && Object.keys(this.state.industrySegments).map((opt,index) =>
      <option key={this.state.industrySegments[opt].id} value={this.state.industrySegments[opt].id}>{this.state.industrySegments[opt].name}</option>
    );
    const businessSegments = this.state.businessSegments && Object.keys(this.state.businessSegments).map((opt,index) =>
      <option key={this.state.businessSegments[opt].id} value={this.state.businessSegments[opt].id}>{this.state.businessSegments[opt].name}</option>
    );
    const departments = this.state.departments && Object.keys(this.state.departments).map((opt,index) =>
      <option key={this.state.departments[opt].id} value={this.state.departments[opt].name}>{this.state.departments[opt].name}</option>
    );
    return(
      <div>
      {this.state.hasError &&
        <Alert bsStyle="danger">
          {this.state.errorMsg}
        </Alert>}
        <form id="registrationForm">
        <Row>
        <FormElement id="userId" labelText="User ID" type="text" name="userId" value={this.state.inputs.userId} {...(this.props.isRegister ? {onChange: this.handleInputChange, required:true} : {readOnly:true})} />
        {this.props.isRegister && <FormElement id="password" labelText="password" type="password" name="password" onChange={this.handleInputChange} required="true"/>}
        </Row>
        <Row>
        <FormElement id="orgName" labelText="Name of the organization" type="text" name="orgName" value={this.state.inputs.orgName} onChange={this.handleInputChange} required="true"/>
        {this.props.isRegister && <FormElement id="gstNo" labelText="GST No" type="text" name="gstNo" value={this.state.inputs.gstNo} onChange={this.handleInputChange} required="true"/>}
        </Row>
        <Row>
        <FormElement id="address" labelText="Address" type="text" name="address" value={this.state.inputs.address} onChange={this.handleInputChange} required="true"/>
        <div className="col-md-6">&nbsp;</div>
        </Row>
        <Row>
        <FormElement id="city" labelText="City/Town" type="text" name="city" value={this.state.inputs.city} onChange={this.handleInputChange} required="true"/>
        <FormElement id="pincode" labelText="Pincode" type="number" name="pincode" value={this.state.inputs.pincode} onChange={this.handleInputChange} required="true"/>
        </Row>
        <Row>
        <FormElement id="state" labelText="State" type="text" name="state" value={this.state.inputs.state} onChange={this.handleInputChange} required="true"/>
        <PhoneNumber onChange={this.handleInputChange} required="true" phoneCode={this.state.inputs.phoneCode} phoneNumber={this.state.inputs.phoneNumber}/>
        </Row>
        <Row>
        <FormElement id="faxNumber" labelText="Fax No" type="number" name="faxNumber" value={this.state.inputs.faxNumber} onChange={this.handleInputChange} required="true"/>
        <FormElement id="website" labelText="Website" type="text" name="website" value={this.state.inputs.website} onChange={this.handleInputChange} required="true"/>
        </Row>
        <Row>
        <FormGroup className="col-md-6">
          <ControlLabel>Industry Segment</ControlLabel>
          <FormControl componentClass="select" id="industrySegment" name="industrySegment" value={this.state.inputs.industrySegment} onChange={this.handleInputChange} mandatory="true">
              <option key='0' value=""> --select one--</option>
              {industrySegments}
          </FormControl>
        </FormGroup>
        <FormGroup className="col-md-6">
          <ControlLabel>Business Segment</ControlLabel>
          <FormControl componentClass="select" id="businessSegment" name="businessSegment" value={this.state.inputs.businessSegment} onChange={this.handleInputChange} mandatory="true">
              <option key='0' value=""> --select one--</option>
              {businessSegments}
          </FormControl>
        </FormGroup>
        </Row>
        <Row>
        <FormElement id="contactPerson" labelText="Contact Person" type="text" name="contactPerson" value={this.state.inputs.contactPerson} onChange={this.handleInputChange} required="true"/>
        <FormElement id="designation" labelText="Designation" type="text" name="designation" value={this.state.inputs.designation} onChange={this.handleInputChange} required="true"/>
        </Row>
        <Row>
        {(this.state.inputs.industrySegment == null || this.state.inputs.industrySegment != 1) && <FormGroup className="col-md-6">
          <ControlLabel>Department</ControlLabel>
          <FormControl componentClass="select" id="department_select" name="department" value={this.state.inputs.department} onChange={this.handleDepartment}>
              <option key='0' value=""> --select one--</option>
              {departments}
          </FormControl>
        </FormGroup>
        }
        {this.state.inputs.industrySegment && this.state.inputs.industrySegment == 1 &&
        <FormElement id="department_text" labelText="Department" type="text" value={this.state.inputs.department} name="department" onChange={this.handleInputChange} required="true"/>
        }
        </Row>
        <Row>
        <FormElement id="mobileNumber" labelText="Mobile No." type="number" name="mobileNumber" value={this.state.inputs.mobileNumber} onChange={this.handleInputChange} required="true"/>
        <FormElement id="emailId" labelText="E-Mail ID" type="text" name="emailId" value={this.state.inputs.emailId} onChange={this.handleInputChange} required="true"/>
        </Row>
        {this.props.isRegister && <Button bsStyle="info" onClick={this.handleSubmit}>Register</Button>}
        {this.props.isRegister == false && <Button bsStyle="info" onClick={this.handleSubmit}>Save General Details</Button>}
        </form>
      </div>
    );
  }
}

export default GeneralDetails;



