import React from 'react';
import AppComponent from '../AppComponent';
import FormElement from '../FormElement';
import {Row, Button, FormGroup, ControlLabel, FormControl, Checkbox, Radio} from 'react-bootstrap';
import {isValidForm} from '../utilities';
import {getSupplierDetails, saveSupplierDetails} from '../../services/profile';

class SupplierDetails extends AppComponent {

  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.setError = this.setError.bind(this);
  }

  async componentDidMount() {
    document.getElementById("typeOfJobs").selectedIndex = -1;
    let financeDetails = await getSupplierDetails(this.state.auth.orgId) || {};
    Object.assign(financeDetails, {'orgId':this.state.auth.orgId});
    this.setState({'inputs':financeDetails});
  }

  setError(errorMessage) {
    this.setState({hasError:true,errorMsg:errorMessage});
  }

  async handleSubmit() {
    this.setState({hasError:false});
    if(isValidForm('supplierdetails')) {
        saveSupplierDetails(this.state.inputs).then(res => {
          if (res.status !== 200) {
            // show general error
            this.setError('Technical issue. Please contact system admin.')
          } else if (res.status === 200) {
            this.setError('Supplier details saved');
          }
        });
    } else {
      this.setError('Please correct the highlighted fields.');
    }
  }

  render() {
    return(
      <div>
        <form id="supplierdetails">
        <Row className="col-md-12" style={{'marginBottom':'20px'}}>
        <FormGroup className="col-md-6" controlId="typeofjob">
          <ControlLabel>Type of Jobs</ControlLabel>
          <FormControl id="typeOfJobs" name="typeOfJobs" componentClass="select" multiple onChange={this.handleInputChange} mandatory="true">
            <option value={1}>Press components</option>
            <option value={2}>Fabricated components</option>
            <option value={3}>Precision Machined components</option>
            <option value={4}>Plastic Moulding components</option>
            <option value={5}>Rubber components</option>
            <option value={6}>Foundry</option>
          </FormControl>
        </FormGroup>
        </Row>
        <Row className="col-md-12">
          <FormGroup className="col-md-12">
            <ControlLabel>Please select the type of industries to which you would provide outsourcing jobs:</ControlLabel>
            <Row>
            <Checkbox className="col-md-3" name="isMicro" id="microindustries" onChange={this.handleInputChange}>
              Micro Industries
            </Checkbox>
            <Checkbox className="col-md-3" style={{'marginTop':'10px'}} name="isSmall" id="smallindustries" onChange={this.handleInputChange}>
              Small Industries
            </Checkbox>
            <Checkbox className="col-md-3" style={{'marginTop':'10px'}} name="isMedium" id="mediumindustries" onChange={this.handleInputChange}>
              Medium Industries
            </Checkbox>
            <Checkbox className="col-md-3" style={{'marginTop':'10px'}} name="isLarge" id="largeindustries" onChange={this.handleInputChange}>
              Large Industries
            </Checkbox>
            </Row>
          </FormGroup>
        </Row>
        <Row className="col-md-12">
          <FormGroup className="col-md-12">
            <ControlLabel>Please select the type of industries to which you would provide outsourcing jobs:</ControlLabel>
            <Row className="col-md-12">
            <Radio name="isoCertification" inline value="yes" onChange={this.handleInputChange} mandatory="true">
              Yes
            </Radio>{' '}
            <Radio name="isoCertification" inline value="no" onChange={this.handleInputChange} mandatory="true">
              No
            </Radio>{' '}
            <Radio name="isoCertification" inline value="not mandatory" onChange={this.handleInputChange} mandatory="true">
              Not Mandatory
            </Radio>
            </Row>
          </FormGroup>
        </Row>
        <Button bsStyle="info" onClick={this.handleSubmit}>Save General Details</Button>
        </form>
      </div>
    );
  }
}

export default SupplierDetails;



