import React from 'react';

class FormElement extends React.Component {
  render() {
    return(
      <div className="col-md-6 form-group ">
        <label className="control-label">{this.props.labelText}</label>
        <input className="form-control" id={this.props.id} type={this.props.type} name={this.props.name} onChange={this.props.onChange} mandatory={this.props.required} validator={this.props.validator} value={this.props.value} readOnly={this.props.readOnly}>
        </input>
      </div>
    );
  }
}

export default FormElement;



