import React from 'react';
import AppComponent from '../AppComponent';
import FormElement from '../FormElement';
import {Row, Alert, Button} from 'react-bootstrap';
import {isValidForm, highlightError} from '../utilities';
import {validateGST} from '../../utils/validator';
import {saveFinanceDetails, getFinanceDetails} from '../../services/profile'

class FinanceDetails extends AppComponent {

  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.setError = this.setError.bind(this);
  }

  async componentDidMount() {
    let financeDetails = await getFinanceDetails(this.state.auth.orgId) || {};
    Object.assign(financeDetails, {'orgId':this.state.auth.orgId});
    this.setState({'inputs':financeDetails});
  }

  setError(errorMessage) {
    this.setState({hasError:true,errorMsg:errorMessage});
  }

  validate() {
    if (isValidForm('financedetails')) {
      var isValid = true;

      if (validateGST(this.state.inputs.gstNo) === false) {
        highlightError('gstNo');
        isValid = false;
      }
      return isValid;
    }
    return true;
  }

  async handleSubmit() {
    this.setState({hasError:false});
    if(isValidForm('financedetails') && this.validate()) {
        saveFinanceDetails(this.state.inputs).then(res => {
          if (res.status !== 200) {
            // show general error
            this.setError('Technical issue. Please contact system admin.')
          } else if (res.status === 200) {
            this.setError('Finance details saved');
          }
        });
    } else {
      this.setError('Please correct the highlighted fields.');
    }
  }

  render() {
    return(
      <div>
      {this.state.hasError &&
        <Alert bsStyle="danger">
          {this.state.errorMsg}
        </Alert>}
      <form id="financedetails">
      <Row>
      <FormElement id="aturn" labelText="Annual Turnover" type="text" name="annualTurnover" value={this.state.inputs.annualTurnover} onChange={this.handleInputChange} required="true"/>
      <FormElement id="bankName" labelText="Name of the Bank" type="text" name="nameOfBank" value={this.state.inputs.nameOfBank} onChange={this.handleInputChange} required="true"/>
      </Row>
      <Row>
      <FormElement id="bankBranch" labelText="Bank Branch" type="text" name="bankBranch" value={this.state.inputs.bankBranch} onChange={this.handleInputChange} required="true"/>
      <FormElement id="accNumber" labelText="Account Number" type="text" name="accountNumber" value={this.state.inputs.accountNumber} onChange={this.handleInputChange} required="true"/>
      </Row>
      <Row>
      <FormElement id="panNo" labelText="Pan Number" type="text" name="panNumber" value={this.state.inputs.panNumber} onChange={this.handleInputChange} required="true"/>
      <FormElement id="tanNo" labelText="Tan Number" type="text" name="tanNumber" value={this.state.inputs.tanNumber} onChange={this.handleInputChange} required="true"/>
      </Row>
      <Row>
      <FormElement id="gstNo" labelText="GST No" type="text" name="gstNo" value={this.state.inputs.gstNo} onChange={this.handleInputChange} required="true"/>
      </Row>
      <Button bsStyle="info" onClick={this.handleSubmit} >Save Finance Details</Button>
      </form>
      </div>
    );
  }
}

export default FinanceDetails;



