import $ from 'jquery';

export function isValidForm(formId) {
  let hasError = false;
  $('#'+formId+ ' input, #'+formId+ ' select').each(function() {
    if($(this).attr('type') === 'radio') {
      let name = $(this).attr('name');
      if($(this).attr('mandatory') === 'true' &&
      (($('input[name='+ name+']:checked').val() === null) ||
      ($('input[name='+ name+']:checked').val() === undefined))) {
        $(this).closest('.form-group').addClass('has-error');
        hasError = true;
      } else {
        $(this).closest('.form-group').removeClass('has-error');
      }
    } else if(
      $(this).attr('mandatory') === 'true' &&
      (($(this).val() === undefined || $(this).val() === null) ||
      ($.isArray($(this).val()) && ($(this).val() === null || $(this).val().length === 0)))
      )
    {
      $(this).parent('.form-group').addClass('has-error');
      hasError = true;
    } else {
      $(this).parent('.form-group').removeClass('has-error');
    }
  });
  return !hasError;
}

export function highlightError(input) {
  let inputElement = $('#'+input);
  inputElement.parent('.form-group').addClass('has-error');
}



