import React from 'react';
import AppComponent from '../components/AppComponent';
import GuardedContainer from '../components/GuardedContainer';
import {Tabs, Tab} from 'react-bootstrap';
import GeneralDetails from '../components/manufacturer/generaldetails';
import FinanceDetails from '../components/manufacturer/financedetails';
import TechDetails from '../components/manufacturer/technicaldetails';
import SupplierDetails from '../components/manufacturer/supplierdetails';

class Profile extends AppComponent {
  render() {
    return(
      <GuardedContainer>
      <Tabs defaultActiveKey={1} id="uncontrolled-tab-example">
        <Tab eventKey={1} title="General" tabClassName="col-md-3">
          <GeneralDetails isRegister={false}/>
        </Tab>
        <Tab eventKey={2} title="Finance" tabClassName="col-md-3">
          <FinanceDetails />
        </Tab>
        <Tab eventKey={3} title="Technical" tabClassName="col-md-3">
          <TechDetails/>
        </Tab>
        <Tab eventKey={4} title="Supplier" tabClassName="col-md-3">
          <SupplierDetails />
        </Tab>
      </Tabs>
      </GuardedContainer>
    );
  }
}

export default Profile;



