import {BASE_URL} from './services'

export function getIndustrySegments() {
  return fetch(BASE_URL + '/industrysegments').then(response => {return response.json();});
}

export function getBusinessSegments() {
  return fetch(BASE_URL + '/businesssegments').then(response => {return response.json();});
}

export function getDepartments() {
  return fetch(BASE_URL + '/departments').then(response => {return response.json();});
}

export function getOrgSize() {
  return fetch(BASE_URL + '/organizationsize').then(response => {return response.json();});
}

export function getClusterType() {
  return fetch(BASE_URL + '/clustertype').then(response => {return response.json();});
}



