import React from 'react';
import AppComponent from '../components/AppComponent';
import GuardedContainer from '../components/GuardedContainer';
import GeneralDetails from '../components/manufacturer/generaldetails';

class Register extends AppComponent {

  constructor(props) {
    super(props);
  }

  render() {
    return(
        <GuardedContainer pageHeader="Manufacturer Registration">
          <GeneralDetails isRegister={true}/>
        </GuardedContainer>
    );

  }
}

export default Register;



