import React from 'react';
import AppComponent from '../AppComponent';
import FormElement from '../FormElement';
import {Row, Alert, Button} from 'react-bootstrap';
import {isValidForm, highlightError} from '../utilities';
import Facilities from './facilities';
import { getTechnicalDetails, saveTechnicalDetails } from '../../services/profile'

class TechDetails extends AppComponent {
  constructor(props){
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.addFacility = this.addFacility.bind(this);
    this.removeFacility = this.removeFacility.bind(this);
    this.getFacilities = this.getFacilities.bind(this);
  }

  async componentDidMount() {
    this.setState({'facilities':[],'facCount':-1});
    let technicalDetails = await getTechnicalDetails(this.state.auth.orgId) || {};
    Object.assign(technicalDetails, {'orgId':this.state.auth.orgId});
    this.setState({'inputs':technicalDetails});
    if(this.state.inputs.facilityDTOs != null && this.state.inputs.facilityDTOs.length > 1) {
      for(let facility in this.state.inputs.facilityDTOs) {
        this.addFacility();
      }
    } else {
      this.addFacility();
    }

  }

  addFacility() {
    const newCount = this.state.facCount + 1;

    let faclts = this.state.facilities;
    let inputs = this.state.inputs;
    if(inputs.facilityDTOs == null || inputs.facilityDTOs == undefined)
    {
      inputs.facilityDTOs = [];
    }
    let facDTOs = inputs.facilityDTOs.push({});
    faclts.push(<Facilities key={newCount} data_key={newCount} onRemove={this.removeFacility} inputs={this.state.inputs}/>);
    this.setState({'facCount':newCount,'facilities':faclts,'inputs':inputs});
  }

  removeFacility(event) {

    const index = event.target.attributes['data_key'].nodeValue;
    this.state.facilities[index]=null;
    this.setState({'facilities':this.state.facilities});
  }

  setError(errorMessage) {
    this.setState({hasError:true,errorMsg:errorMessage});
  }

  getFacilities() {
    let facilities = this.state.inputs.facilityDTOs;
    let newFacilities = [];
    let index=0;
    for(;facilities.length < index;index++) {
      let facility = facilities[index];
      if(facility != null) {
        newFacilities.push(facility);
      }
    }
    return newFacilities;
  }

  async handleSubmit() {
    this.setState({hasError:false});
    if(isValidForm('technicaldetails')) {
        let facilities = this.getFacilities();
        let inputs = this.state.inputs;
        inputs.facilityDTOs = facilities;
        saveTechnicalDetails(inputs).then(res => {
          if (res.status !== 200) {
            // show general error
            this.setError('Technical issue. Please contact system admin.')
          } else if (res.status === 200) {
            this.setError('Technical details saved');
          }
        });
    } else {
      this.setError('Please correct the highlighted fields.');
    }
  }

  render() {
    const facilities = this.state.facilities && this.state.facilities.map((facility, index)=>
        facility
    )
    return(
      <div>
      {this.state.hasError &&
        <Alert bsStyle="danger">
          {this.state.errorMsg}
        </Alert>}
      <form id="technicaldetails">
      <Row>
        <FormElement className="col-offset-md-4" id="products" labelText="Product(s) Manufactured" type="text" name="products" onChange={this.handleInputChange} required="true"/>
      </Row>
        {facilities}
        <Button className="pull-right" bsStyle="info" onClick={this.addFacility}>Add Facility</Button>
        <Button bsStyle="info" onClick={this.handleSubmit}>Submit</Button>
      </form>
      </div>
    );
  }
}

export default TechDetails;



