import React, {Component} from 'react';
import logo from '../img/headerlogo.png';

class Header extends Component {

	constructor(props) {
		super(props);
		this.signOut = this.signOut.bind(this);
		this.init();
	}

	init() {
		if(this.props.isGuarded) {
			if(sessionStorage.getItem('authToken') === null) {
				window.location.href = '/';
			}
		}
	}

	signOut() {
		sessionStorage.removeItem('authToken');
		window.location.href = "/";
		//this.props.history.push('/home');
	}

	render() {
		return(
				<div className="col-md-12 header">
		    	<div className="col-md-1"></div>
					<div className="col-md-2">
						<a href="/">
						<img alt="logo" src={logo} className="headerLogo">
						</img>
						</a>
					</div>
					<div className="col-md-2" >
						<div style={{'fontSize': 'smaller'}}>Powered by</div>
						<div style={{'fontSize': 'small','fontWeight': 'bolder'}}> CircumFusion</div>
    			</div>
					<div className="col-md-4"></div>
					{ this.props.isGuarded &&
					<div className="col-md-2" >
						<i className="fa fa-user-circle" aria-hidden="true" style={{'color': '#9d9d9d','fontSize': '31px'}}></i>
						<span style={{'fontSize': 'small','fontWeight': 'bolder','marginLeft':'10px'}}>Hi UserName </span>
	          <i className="fa fa-chevron-down" aria-hidden="true" style={{'fontSize': 'smaller !important','color': '#635f5f'}}></i>
						<i className="fa fa-sign-out" style={{'cursor':'pointer','fontSize':'2em','marginLeft':'10px'}} onClick={this.signOut}></i>
					</div>

					}
          <div className="col-md-1"></div>
      	</div>
			);
	}
}

export default Header;



