import {BASE_URL} from './services'

export function registerManufacturer(params) {
  return fetch(BASE_URL + '/register/manufacturer',{
    method:'post',
    body: JSON.stringify(params),
    headers: {'Content-Type':'application/json'}
  }).then(res => {
    return res;
  });
}

export function registerService(params) {
  return fetch(BASE_URL + '/register/serviceindustry',{
    method:'post',
    body: JSON.stringify(params),
    headers: {'Content-Type':'application/json'}
  }).then(res => {
    return res;
  });
}



