import React from 'react';
import Header from './Header';
import Container from './Container';
import {Alert} from 'react-bootstrap';

class GuardedContainer extends React.Component {
  render() {
    return (
      <div>
        <Header isGuarded={sessionStorage.getItem('authToken') !== null}/>
        <Container pageHeader={this.props.pageHeader} column={this.props.column}>
          {this.props.children}
        </Container>
      </div>
    );
  }
}

export default GuardedContainer;



