import React from 'react';
import {FormGroup,ControlLabel,FormControl} from 'react-bootstrap';

class PhoneNumber extends React.Component {
  render() {
    return(
      <FormGroup className="col-md-6">
        <ControlLabel>Phone Number</ControlLabel>
        <div style={{width: '100%'}}>
        <FormControl className="col-xs-2" id="phoneCode" type="text"  name="phoneCode" style={{width: '16.66667%'}} onChange={this.props.onChange} mandatory={this.props.required} value={this.props.phoneCode}/>
        <FormControl className="col-xs-4" id="phoneNumber" type="number"  name="phoneNumber" style={{width: '80.33333%',marginLeft:'3%'}} onChange={this.props.onChange} mandatory={this.props.required} value={this.props.phoneNumber}/>
        </div>
      </FormGroup>
    );
  }
}

export default PhoneNumber;



